@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1px)) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: ($vp-1440 - 1px)) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: ($vp-1280 - 1px)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 1px)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}

@mixin vp-374 {
  @media (max-width: ($vp-375 - 1px)) {
    @content;
  }
}

@mixin font-face($font, $src, $font-weight: normal, $font-style: normal) {
  @font-face {
    font: {
      family: $font;
      weight: $font-weight;
      style: $font-style;

      display: swap;
    }

    src: url("../fonts/#{$src}.woff2") format("woff2"),
      url("../fonts/#{$src}.woff") format("woff");
  }
}

@mixin font($font-family: inherit, $font-weight: inherit, $font-size: inherit, $line-height: inherit, $color: inherit) {
  font: {
    weight: $font-weight;
    size: $font-size;
    family: $font-family;
  }

  line-height: $line-height;
  color: $color;
}

